@charset "UTF-8";
*,
*::before,
*::after {
  box-sizing: border-box;
}

html {
  font-family: sans-serif;
  line-height: 1.15;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  -ms-overflow-style: scrollbar;
  -webkit-tap-highlight-color: transparent;
}

@-ms-viewport {
  width: device-width;
}

article, aside, dialog, figcaption, figure, footer, header, hgroup, main, nav, section {
  display: block;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
  font-size: 1rem;
  font-weight: 400;
  line-height: 1.5;
  color: #212529;
  text-align: left;
  background-color: #fff;
}

[tabindex="-1"]:focus {
  outline: none !important;
}

hr {
  box-sizing: content-box;
  height: 0;
  overflow: visible;
}

h1, h2, h3, h4, h5, h6 {
  margin-top: 0;
  margin-bottom: 0.5rem;
}

p {
  margin-top: 0;
  margin-bottom: 1rem;
}

abbr[title],
abbr[data-original-title] {
  text-decoration: underline;
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
  cursor: help;
  border-bottom: 0;
}

address {
  margin-bottom: 1rem;
  font-style: normal;
  line-height: inherit;
}

ol,
ul,
dl {
  margin-top: 0;
  margin-bottom: 1rem;
}

ol ol,
ul ul,
ol ul,
ul ol {
  margin-bottom: 0;
}

dt {
  font-weight: 700;
}

dd {
  margin-bottom: .5rem;
  margin-left: 0;
}

blockquote {
  margin: 0 0 1rem;
}

dfn {
  font-style: italic;
}

b,
strong {
  font-weight: bold;
}

small {
  font-size: 80%;
}

sub,
sup {
  position: relative;
  font-size: 75%;
  line-height: 0;
  vertical-align: baseline;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

a {
  color: #007bff;
  text-decoration: none;
  background-color: transparent;
  -webkit-text-decoration-skip: objects;
}

a:hover {
  color: #0056b3;
  text-decoration: underline;
}

a:not([href]):not([tabindex]) {
  color: inherit;
  text-decoration: none;
}

a:not([href]):not([tabindex]):focus, a:not([href]):not([tabindex]):hover {
  color: inherit;
  text-decoration: none;
}

a:not([href]):not([tabindex]):focus {
  outline: 0;
}

pre,
code,
kbd,
samp {
  font-family: monospace, monospace;
  font-size: 1em;
}

pre {
  margin-top: 0;
  margin-bottom: 1rem;
  overflow: auto;
  -ms-overflow-style: scrollbar;
}

figure {
  margin: 0 0 1rem;
}

img {
  vertical-align: middle;
  border-style: none;
}

svg:not(:root) {
  overflow: hidden;
}

a,
area,
button,
[role="button"],
input:not([type="range"]),
label,
select,
summary,
textarea {
  -ms-touch-action: manipulation;
  touch-action: manipulation;
}

table {
  border-collapse: collapse;
}

caption {
  padding-top: 0.75rem;
  padding-bottom: 0.75rem;
  color: #868e96;
  text-align: left;
  caption-side: bottom;
}

th {
  text-align: inherit;
}

label {
  display: inline-block;
  margin-bottom: .5rem;
}

button {
  border-radius: 0;
}

button:focus {
  outline: 1px dotted;
  outline: 5px auto -webkit-focus-ring-color;
}

input,
button,
select,
optgroup,
textarea {
  margin: 0;
  font-family: inherit;
  font-size: inherit;
  line-height: inherit;
}

button,
input {
  overflow: visible;
}

button,
select {
  text-transform: none;
}

button,
html [type="button"],
[type="reset"],
[type="submit"] {
  -webkit-appearance: button;
}

button::-moz-focus-inner,
[type="button"]::-moz-focus-inner,
[type="reset"]::-moz-focus-inner,
[type="submit"]::-moz-focus-inner {
  padding: 0;
  border-style: none;
}

input[type="radio"],
input[type="checkbox"] {
  box-sizing: border-box;
  padding: 0;
}

input[type="date"],
input[type="time"],
input[type="datetime-local"],
input[type="month"] {
  -webkit-appearance: listbox;
}

textarea {
  overflow: auto;
  resize: vertical;
}

fieldset {
  min-width: 0;
  padding: 0;
  margin: 0;
  border: 0;
}

legend {
  display: block;
  width: 100%;
  max-width: 100%;
  padding: 0;
  margin-bottom: .5rem;
  font-size: 1.5rem;
  line-height: inherit;
  color: inherit;
  white-space: normal;
}

progress {
  vertical-align: baseline;
}

[type="number"]::-webkit-inner-spin-button,
[type="number"]::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  outline-offset: -2px;
  -webkit-appearance: none;
}

[type="search"]::-webkit-search-cancel-button,
[type="search"]::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  font: inherit;
  -webkit-appearance: button;
}

output {
  display: inline-block;
}

summary {
  display: list-item;
}

template {
  display: none;
}

[hidden] {
  display: none !important;
}

.Icon {
  /* Permet de redimensionner en changeant la font-size de l’icône */
  width: 1em;
  height: 1em;
  /* Bel alignement visuel des icônes avec le texte */
  vertical-align: -0.15em;
  /* fill par défaut = valeur de la propriété couleur de son élément parent */
  fill: currentColor;
  /* Les paths et les strokes qui dépassent de la viewBox peuvent apparaître dans IE.
     Si vous utilisez normalize.css, inutile de l’ajouter. */
  overflow: hidden;
}

@font-face {
  font-family: "Police_perso";
  src: url("../fonts/BerninaSans-Web-CondensedBold.woff2");
}

.background {
  width: 100%;
}

.text_pub {
  font-weight: bold;
}

.text_plus_petit {
  font-size: 12px;
}

body {
  background-color: #f7ffff;
}

body, article, footer, header {
  font-family: "Police_perso", Helvetica, Arial, sans-serif;
}

header {
  position: relative;
}

.rdv, h2 {
  color: #00465b;
}

.rdv {
  background: rgba(255, 255, 255, 0);
  width: 150px;
  height: 50px;
  margin: 5px;
}

p {
  color: #00465b;
}

.pub {
  background-color: #cc0000;
  width: 200px;
  position: absolute;
  top: 60px;
  left: 170px;
  border-radius: 10px;
  text-align: center;
  transform: rotate(25deg);
}

.pub p {
  margin: 0;
  color: white;
}

.bandeau_background {
  position: absolute;
  background-color: rgba(255, 255, 255, 0.5);
  top: 200px;
  width: 100%;
  color: white;
  font-size: 10px;
}

.bandeau_background p, .bandeau_background h2 {
  color: white;
}

.inner {
  width: 90%;
  margin-left: 20px;
  margin-right: 20px;
}

.footer_img {
  width: 150px;
  text-align: center;
}

.footer_img img {
  width: 150px;
}

.arrondir {
  border: 5px solid white;
  border-radius: 100px;
}

.active {
  order: 1;
}

.top {
  display: flex;
  justify-content: space-between;
}

.top .img {
  width: 200px;
}

.center {
  text-align: center;
  color: #00465b;
}

footer {
  margin-left: 20%;
  margin-right: 20%;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}

/**body{
  background-position: center;
  background-repeat:no-repeat;
  background-size: 100%;
}**/
@media (min-width: 556px) {
  .inner h2 {
    display: block;
  }
  .active {
    order: -1;
  }
  .pub {
    top: 60px;
    left: 350px;
  }
  .bandeau_background {
    top: 350px;
  }
}

@media (min-width: 834px) {
  footer {
    justify-content: space-between;
    margin: 0px;
  }
  .pub {
    top: 60px;
    left: 500px;
  }
  .bandeau_background {
    top: 450px;
  }
  .inner {
    width: 60%;
    margin-left: 20%;
    margin-right: 20%;
  }
}

@media (min-width: 1200px) {
  .pub {
    top: 60px;
    left: 900px;
  }
  .bandeau_background {
    top: 650px;
  }
  footer {
    justify-content: space-between;
  }
}

@media (min-width: 1900px) {
  .pub {
    top: 60px;
    left: 1600px;
  }
  .bandeau_background {
    top: 1050px;
  }
}
